@import"customs";

  .gos-footer {
      background-color: $primary;
    height: $grid-gutter-width*14;
    position: relative;
    bottom: 0px;

      .gos-footer-container {

        .gos-footer-inner{

          .gos-footer-header {
            border-bottom: 2px solid $secondary;
          }

          h2{
            color: $secondary;
            font-size: $grid-gutter-width*1.5;

          }
          a {
            @include gos-footer-link;
          }

        }

      }

  }

