
   @import "customs";
   @import "mixins";

   #defaultCanvas0 { overflow:hidden; }
    .overlay {
    background-image: url("https://64.media.tumblr.com/96bfcda9ab211b730d680a052d19f83d/tumblr_o7w2veChcr1runoqyo4_540.gifv");
    height: 100%;
    width: 100%;
    opacity: 0.15;
    background-repeat:no-repeat;
    background-size: cover;
    position: absolute;
  } 
   svg {
     position: absolute;
     width: 100%;
     height: 100%;
   }

    circle {
       stroke-width: 2;
       cursor: pointer;
       transition: 0.5s fill, 0.5s stroke-width;
   }
   circle:hover {
       fill: #5e5ee5;
       stroke-width: 18;
   } 





