
   @import "customs";
   @import "mixins";

   #map {top:0; bottom:0; height: 100vh; }

   svg {
     position: absolute;
     width: 100%;
     height: 100%;
   }

    circle {
       stroke-width: 2;
       cursor: pointer;
       transition: 0.5s fill, 0.5s stroke-width;
   }
   circle:hover {
       fill: #5e5ee5;
       stroke-width: 18;
   } 





   .gos-general-infotab {
    display: fixed;
    background-color: transparent;
    width: 100vw;
    height: 10%;
    position: absolute;
    top:0;
    left:0;
    border: 2px solid $primary;
    webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
    background-color: rgba(255, 255, 255, 0.5);
    border-radius: 5px;
    
    @include media-breakpoint-up(lg) {
    display: fixed;
    background-color: transparent;
    width: 40vw;
    height: 60%;
    position: absolute;
    top:0;
    left: 0;
    border-bottom: 2px solid $primary;
        
    }

    .gos-general-infotab-container{

      .info-inner{
        font-size: $grid-gutter-width*0.75;
        font-family: Oswald;
        font-weight: 300;
        border-bottom: 2px solid black;
        h2{
          font-family: Oswald;
          font-weight: 400;
        }

        .info{
          font-family: work sans;
          font-size: $grid-gutter-width*1.25;
        }
      }

    }

}